<template>
  <div class="container" style="margin: 15px 15px;">
    <!-- 页面名字 -->
    <el-row type="flex" justify="start" style=" font-size: 16px; color: #303133;">
      <router-link class='el-button--text' to="/adviser/list" replace>顾问管理</router-link>
      / {{pageTitle}}
    </el-row>
    <el-row type="flex" justify="start" style="margin-top: 30px;">
     <el-form ref="form" :model="form" label-width="90px" label-suffix=":" label-position="left">
        <el-form-item label="顾问姓名" prop="name" required>
          <el-input v-model="form.name" placeholder="请输入顾问姓名"></el-input>
        </el-form-item>
        <el-form-item label="顾问图片" required>
          <div v-if="form.avatar && form.avatar.id" class="avatar">
            <img :src="'https://szebra.cn' + form.avatar.url" />
            <i class="el-icon-circle-close" @click="removeConverImageUrl" />
          </div>
          <el-upload v-else class="avatar-uploader"
                    action="https://szebra.cn/api/admin/images"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    name="image[file]"
                    :before-upload="beforeAvatarUpload">
            <i class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="职称描述" prop="desc" required>
          <el-input v-model="form.desc" placeholder="请输入职称描述" />
        </el-form-item>
        <el-form-item label="星级" required>
          <el-select v-model="form.level">
            <el-option v-for="item in [1,2,3,4,5]" :key="item" :label="item" :value="item" />
          </el-select>
        </el-form-item>
        <el-form-item label="顾问电话" prop="name" required>
          <el-input v-model="form.mobile" placeholder="请输入顾问电话" />
        </el-form-item>
        <el-form-item label="服务楼盘" required>
          <el-select v-model="form.building_id" filterable placeholder="请选择服务楼盘">
            <el-option v-for="item in buildingList" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-button type="primary" @click="onSubmit" style="">{{ submitBtnTitle }}</el-button>
      </el-form>
    </el-row>
  </div>
</template>
<script>

export default {
  components: {
  },
  data () {
    return {
      submitFuncName: '',
      pageState: '', // 1新增，2编辑，3预览
      pageTitle: '', // 页面标题
      submitBtnTitle: '',
      form: {
        avatar: { id: '', url: '' },
        desc: '',
        id: '',
        level: 1,
        mobile: '',
        name: '',
        building_id: ''
      },
      buildingList: []
    }
  },
  methods: {
    submit () {
      const data = this.formatSubmitValue()
      this.$http[this.submitFuncName](data).then(res => {
        if (!res) return
        this.$message.success('保存成功')
      })
    },
    formatSubmitValue () {
      if (!this.form.avatar.id) {
        this.$message.error('顾问图片不可为空')
      }

      const ret = { id: this.form.id, adviser: Object.assign({}, this.form) }
      ret.adviser.avatar_attributes = { id: this.form.avatar.id }
      ret.adviser.id = ret.adviser.avatar = undefined
      return ret
    },
    onSubmit () {
      this.$refs.form.validate()
      .then(valid => this.submit())
      .catch(() => {})
    },
    removeConverImageUrl () {
      this.form.avatar.id = this.form.avatar.url = ''
    },
    handleAvatarSuccess(data) {
      this.form.avatar = data.result_rows
    },
    beforeAvatarUpload(file) {
      const isImg = file.type.startsWith('image/');
      const isLt2M = file.size / 1024 / 1024 < 5;
      if (!isImg) {
        this.$message.error('只允许上传图片');
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 5MB!');
      }
      return isImg && isLt2M;
    }
  },
  created () {
    this.$http.buildingList({ per: 1000, page: 0 }).then(res => {
      if (!res) return
      this.buildingList = res.data.result_rows.buildings
    })
    switch (this.$router.currentRoute.name) {
        case 'adviser_create':
          this.pageTitle = '新增顾问'
          this.submitBtnTitle = '提交'
          this.submitFuncName = 'adviserCreate'
          break
        default:
          this.pageTitle = '编辑顾问'
          this.submitBtnTitle = '保存所有编辑结果'
          this.submitFuncName = 'adviserUpdate'
          this.$http.adviserDetail(this.$router.currentRoute.params)
            .then(res => {
              if (!res) return
              Object.assign(this.form, res.data.result_rows)
            })
      }
  }
}
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    position: relative;
    float: left;
    margin-right: 5px;
  }
  .avatar img {
    margin-top: 6px;
    margin-right: 6px;
    width: 178px;
    height: 178px;
  }
  .avatar i {
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0.6;
  }
  .avatar i:hover {
    opacity: 1;
    cursor: pointer;
  }
</style>
